.publish-task{
  .step-title{
    line-height: 20px;
    font-size: 16px;
  }
  .form-group{
    label{
      font-weight:normal;
      text-align: right;
      line-height: 30px;
    }
    .pay-integral{
      font-size: 14px;
      font-weight: bold;
    }
    .btn-radio{
      background: url("../images/btn-radio.png") no-repeat;
    }
    .btn-radio2{
      background: url("../images/btn-radio2.png") no-repeat;
      background-size: 100% 100%;
    }
    .uploadImg{
      img{
        width: 80px;
        height: 80px;
      }
      button{
        margin:2px 0;
      }
      input[type=file]{
        position: relative;
        width: 82px;
        height:34px;
        top: -34px;
        opacity: 0;
        cursor: pointer;
      }
    }
    .review{
      width: 100%;
      height: 60px;
      background-color: #fcfbf9;
      line-height: 60px;
      padding-left: 20px;
      margin-top: 10px;
      input{
        height: 30px;
        width: 50px;
      }
    }
    textarea{
      margin-top: 10px;
    }
  }
  .uploadps{
    margin-top: -40px;
  }
}